/* Top Bar */

.top_bar {
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 64px;
  width: calc(100vw - 64px);
  height: 50px;

  z-index: 1000;
}

.top_list {
  list-style: none;
  display: flex;
  flex-direction: row-reverse;
  color: #f3f4f7;
  padding-right: 2.5vw;
  margin: 0;
}

.top_item {
  display: flex;
  justify-content: right;
  font-size: 1.5em;
  padding-right: 0.75vw;
  padding-top: 0.5vw;
  padding-bottom: 0.5vw;
  color: #3B80C4;
}

.top_icons {
  font-size: 1.5em;
  color: #3B80C4;
}


.top_iconsa {
  font-size: 0.75em;
  color: #3B80C4;
  padding-top: 0.75em;
}

  

.nav_wrap {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #97D1F4;
  width: 64px;
  height: 100vh;
  z-index: -1;
}

.logo_wrap a {
  display: flex;
  justify-content: center;
  height: 50px;
}

.logo_wrap a img {
  height: 100%;
  width: 100%;
}

.logo_wrap {
  height: 25px;
  margin-bottom: 85%;
}

.nav_list {
  margin: 0 auto;
  padding: 0;
}

.nav_item {
  display: flex;
  justify-content: center;
  font-size: 1.1em;
  margin-bottom: 20%;
}

.nav_square {
  padding: 12%;
  border-radius: 1.4vw;
  width: 2vw;
  height: 2vw;
  -webkit-transition: .3s all ease-in-out;
  transition: .3s all ease-in-out;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav_square:hover {
  background-color: #B3DCF4;
}
.nav_square:focus {
  background-color: #2D98DA;
  box-shadow: 0px 3px 6px #00000029;
}

.nav_list {
  list-style: none;
}

.nav_icon {
  color: #fff;
